import { format } from "date-fns";
import { Tagger, TrackingItem } from "./types";
import { mutateAppState, reduxStore } from "./store";
import { formatDay } from "./utils";
import { loadItemsFromLocalStorage, saveItemsToLocalStorage } from "./storage";

const CLIENT_ID = "328707944394-ev19dnlf5guu0vn8p9cn6u0lckqbfhru.apps.googleusercontent.com";
const spreadsheetIds = new URLSearchParams(window.location.search).getAll("id") || [];

export const LOCAL_STORAGE_PENDING_ADD_KEY = "notes_pending_add";

const client = new Promise(resolve => {
  gapi.load("client:auth2", () => {
    resolve(
      gapi.client.init({
        clientId: CLIENT_ID,
        discoveryDocs: ["https://sheets.googleapis.com/$discovery/rest?version=v4"],
        scope: "https://www.googleapis.com/auth/spreadsheets",
      })
    );
  });
});

interface PendingItem {
  id: number;
  timestamp: Date;
  text: string;
}

function getPendingItems() {
  let pendingItems: PendingItem[] = [];
  try {
    pendingItems = JSON.parse(
      window.localStorage[LOCAL_STORAGE_PENDING_ADD_KEY] || "[]"
    ) as PendingItem[];
  } catch (e) {
    console.error("failed to parse pending notes to add", e);
  }
  for (const item of pendingItems) {
    item.timestamp = new Date(item.timestamp as any as string);
  }
  return pendingItems;
}

function addPendingItem(timestamp: Date, text: string) {
  const pendingItems = getPendingItems();
  const id = 1 + pendingItems.reduce((acc, item) => Math.max(acc, item.id), 0);
  pendingItems.push({
    id,
    text,
    timestamp,
  });
  window.localStorage[LOCAL_STORAGE_PENDING_ADD_KEY] = JSON.stringify(pendingItems);
  return id;
}

function deletePendingItem(id: number) {
  const existingPending = getPendingItems();
  const newPending = existingPending.filter(o => o.id !== id);
  if (newPending.length + 1 !== existingPending.length) throw Error("BUG: deletePendingItem");
  window.localStorage[LOCAL_STORAGE_PENDING_ADD_KEY] = JSON.stringify(newPending);
}

async function addPendingNotes() {
  for (const item of getPendingItems()) {
    await addNoteInternal(spreadsheetIds[0], item.timestamp, item.text);
    deletePendingItem(item.id);
  }
}

const handleSignInChange = API(async (signedIn: boolean) => {
  mutateAppState(d => {
    d.googleSignedIn = signedIn;
  });

  if (signedIn) {
    await addPendingNotes();
  }
});

client.then(
  API(async () => {
    const signedIn = gapi.auth2.getAuthInstance().isSignedIn.get();
    await handleSignInChange(signedIn);

    gapi.auth2.getAuthInstance().isSignedIn.listen(handleSignInChange);
  })
);

const sheetIds = (async function (resolve, reject) {
  const result: { [spreadsheetId: string]: number } = {};
  await client;
  for (const spreadsheetId of spreadsheetIds) {
    const resp = await gapi.client.sheets.spreadsheets.get({
      spreadsheetId: spreadsheetId,
      fields: "sheets.properties",
    });

    result[spreadsheetId] = resp.result.sheets![0].properties!.sheetId!;
  }
  return result;
})();

export function updateItemKey(item: TrackingItem) {
  item.key = item.date + " " + item.text;
}

loadItemsFromLocalStorage();

function API<T extends any[], U>(func: (...args: T) => Promise<U>) {
  return async (...args: T): Promise<U> => {
    mutateAppState(d => {
      d.busy++;
    });

    try {
      return await func(...args);
    } finally {
      mutateAppState(d => {
        d.busy--;
      });
    }
  };
}

export const loadRange = API(
  async (spreadsheetId: string, rangeSpec?: { rowNum: number; count: number }) => {
    await client;

    let startRowNum = 1;
    let range = "A:B";
    if (rangeSpec) {
      startRowNum = rangeSpec.rowNum;
      range = `A${rangeSpec.rowNum}:B${rangeSpec.rowNum + rangeSpec.count}`;
    }

    const resp = await gapi.client.sheets.spreadsheets.values.get({
      spreadsheetId,
      range,
    });

    return (resp.result.values || [])
      .filter(row => row[0])
      .map(row => {
        const date = new Date(row[0]);
        const day = formatDay(date);

        const item: TrackingItem = {
          spreadsheetId,
          rowNum: startRowNum++,
          key: "",
          day,
          date,
          text: row[1],
        };
        updateItemKey(item);
        return item;
      });
  }
);

export const loadData = API(async () => {
  let items: TrackingItem[] = [];
  for (const spreadsheetId of spreadsheetIds) {
    const itemsForSheet = await loadRange(spreadsheetId);
    items = items.concat(itemsForSheet);
  }

  mutateAppState(d => {
    d.trackingItems = items;
  });

  saveItemsToLocalStorage(items);
});

export const deleteItem = API(async (item: TrackingItem) => {
  const sheetId = (await sheetIds)[item.spreadsheetId];

  const existingItems = await loadRange(item.spreadsheetId, { rowNum: item.rowNum, count: 1 });
  if (existingItems[0].key !== item.key) {
    throw Error(
      `Unable to delete. Please reload data. Key at row ${item.rowNum} is ${existingItems[0].key}`
    );
  }

  await gapi.client.sheets.spreadsheets.batchUpdate({
    spreadsheetId: item.spreadsheetId,
    resource: {
      requests: [
        {
          deleteDimension: {
            range: {
              sheetId,
              dimension: "ROWS",
              startIndex: item.rowNum - 1,
              endIndex: item.rowNum,
            },
          },
        },
      ],
    },
  });
});

export const sheetDateFormat = "M/d/yyyy H:mm:ss";

export const updateItem = API(async (item: TrackingItem) => {
  const existingItems = await loadRange(item.spreadsheetId, { rowNum: item.rowNum, count: 1 });
  if (existingItems[0].key !== item.key) {
    throw Error(
      `Unable to edit item. Please reload data. Key at row ${item.rowNum} is ${existingItems[0].key}`
    );
  }

  await gapi.client.sheets.spreadsheets.values.update({
    spreadsheetId: item.spreadsheetId,
    range: `A${item.rowNum}:B${item.rowNum}`,
    valueInputOption: "RAW",
    resource: {
      values: [[format(item.date, sheetDateFormat), item.text]],
    },
  });
});

async function addNoteInternal(spreadsheetId: string, date: Date, text: string) {
  const resp = await gapi.client.sheets.spreadsheets.values.append({
    spreadsheetId,
    range: "A:A",
    valueInputOption: "RAW",
    resource: {
      values: [[format(date, sheetDateFormat), text]],
    },
  });

  const range = resp.result.updates?.updatedRange!;
  const rowNum = parseInt(/!A(\d+)/.exec(range)![1], 10);
  const day = formatDay(date);

  mutateAppState(d => {
    const item = {
      spreadsheetId,
      rowNum,
      text,
      day,
      date,
      key: "",
    };
    updateItemKey(item);
    d.trackingItems.push(item);
  });

  saveItemsToLocalStorage(reduxStore.getState().trackingItems);
}

export const addNote = API(async (timestamp: Date, text: string) => {
  await client;

  const id = addPendingItem(timestamp, text);

  await addNoteInternal(spreadsheetIds[0], timestamp, text);

  deletePendingItem(id);
});

interface sheetInfo {
  id: number;
  title: string;
}

async function getSheets(): Promise<sheetInfo[]> {
  const resp = await gapi.client.sheets.spreadsheets.get({
    spreadsheetId: spreadsheetIds[0],
    fields: "sheets.properties",
  });

  return resp.result.sheets!.map(
    o =>
      ({
        id: o.properties!.sheetId,
        title: o.properties!.title,
      } as sheetInfo)
  );
}

async function createSheet(title: string) {
  await gapi.client.sheets.spreadsheets.batchUpdate({
    spreadsheetId: spreadsheetIds[0],
    resource: {
      requests: [
        {
          addSheet: {
            properties: {
              title,
            },
          },
        },
      ],
    },
  });
}

export interface Settings {
  taggers: Tagger[];
}

const settingsSheetTitle = "settings";

export const saveSettings = API(async (settings: Settings) => {
  const settingsSheet = (await getSheets()).find(o => o.title.toLowerCase() === settingsSheetTitle);
  if (!settingsSheet) {
    await createSheet(settingsSheetTitle);
  }

  await gapi.client.sheets.spreadsheets.values.update({
    spreadsheetId: spreadsheetIds[0],
    range: "settings!A1",
    valueInputOption: "RAW",
    resource: {
      values: [[JSON.stringify(settings, null, 3)]],
    },
  });
});

export const loadSettings = API(async (): Promise<Settings> => {
  const resp = await gapi.client.sheets.spreadsheets.values.get({
    spreadsheetId: spreadsheetIds[0],
    range: "settings!A1",
  });

  return JSON.parse(resp.result.values![0][0]) as Settings;
});
