import { differenceInMinutes, format } from "date-fns";

export function formatDay(d: Date) {
  return format(d, "yyyyMMdd");
}

export function formatDayForHuman(d: Date) {
  return format(d, "yyyy-MM-dd EEE");
}

export function formatDiff(a: Date, b: Date): string {
  let diff = differenceInMinutes(a, b);
  const sign = diff < 0 ? "-" : "";
  diff = Math.abs(diff);
  const hours = Math.trunc(diff / 60);
  const minutes = "0" + Math.trunc(diff % 60).toString();
  return sign + hours + ":" + minutes.substr(minutes.length - 2);
}

export function errorString(e: any): string {
  if (e.result) {
    if (e.result.error) {
      return JSON.stringify(e.result.error);
    }

    return JSON.stringify(e.result);
  }

  return String(e);
}
