import React, { useEffect, useState } from "react";
import styles from "./Sheet.module.scss";
import { formatDiff } from "./utils";
import { mutateAppState, useAppState } from "./store";

const RefTime: React.FC = () => {
  const referenceDate = useAppState(o => o.referenceDate);
  const referenceDateNow = useAppState(o => o.referenceDateNow);

  const [nowTime, setNowTime] = useState(new Date());

  useEffect(() => {
    const interval = window.setInterval(() => {
      setNowTime(new Date());
      if (referenceDateNow) {
        mutateAppState(d => {
          d.referenceDate = new Date();
        });
      }
    }, 15_000);
    return () => clearInterval(interval);
  }, [referenceDateNow]);

  function handleSetRefTimeToNowClick() {
    mutateAppState(d => {
      d.referenceDate = new Date();
      d.referenceDateNow = true;
    });
  }

  return (
    <>
      <button disabled={referenceDateNow} onClick={handleSetRefTimeToNowClick}>
        Set ref. time to now
      </button>
      {!referenceDateNow && (
        <span className={styles.nowTimeDiff}>
          {formatDiff(nowTime, referenceDate)} since reference time
        </span>
      )}
    </>
  );
};

export default RefTime;
