import React, { Fragment, useEffect, useMemo } from "react";
import Day from "./Day";
import { TagTotalsByDay, TrackingItem } from "./types";
import { createGetItemsByDay } from "./memos";
import styles from "./Days.module.scss";
import { LOCAL_STORAGE_GAP_MIN_KEY, mutateAppState, useAppState } from "./store";
import Paginator from "./Paginator";

interface Props {
  items: TrackingItem[];
  allDaysDisplaySorted: string[];
  filtered: boolean;
  tagTotalsByDay: TagTotalsByDay | undefined;
}

const DaysInner: React.FC<Props> = ({ items, filtered, tagTotalsByDay, allDaysDisplaySorted }) => {
  const busy = useAppState(o => o.busy) > 0;
  const sortAscending = useAppState(o => o.sortAscending);
  const gapMin = useAppState(o => o.gapMin);

  const getItemsByDay = useMemo(() => createGetItemsByDay("DaysInner"), []);
  const itemsByDay = getItemsByDay(items, sortAscending);

  const pageIndex = useAppState(o => o.pageIndex);
  const pageSize = 10;

  const pageOffset = pageSize * pageIndex;
  const currentPageDays = itemsByDay.sortedDays.slice(pageOffset, pageOffset + pageSize);

  function reverseSort(day: string) {
    const dayId = "day-" + day;
    const clientY = Math.round(document.getElementById(dayId)!.getBoundingClientRect().y);

    mutateAppState(d => {
      d.sortAscending = !d.sortAscending;
      d.pageIndex = Math.floor(
        itemsByDay.sortedDays
          .slice()
          .reverse()
          .findIndex(o => o === day) / pageSize
      );
    });
    requestAnimationFrame(() => {
      const dayElem = document.getElementById(dayId)!;
      document.getElementById("scroll-root")!.scrollTo(0, dayElem.offsetTop - clientY);
    });
  }

  function scrollToDay(day: string) {
    requestAnimationFrame(() => {
      mutateAppState(d => {
        d.pageIndex = Math.floor(itemsByDay.sortedDays.findIndex(o => o === day) / pageSize);
      });
      document.getElementById("day-" + day)!.scrollIntoView();
    });
  }

  useEffect(() => {
    localStorage[LOCAL_STORAGE_GAP_MIN_KEY] = gapMin;
  }, [gapMin]);

  const dayToIndex = useMemo(() => {
    const result = new Map<string, number>();
    for (let i = 0; i < allDaysDisplaySorted.length; i++) {
      result.set(allDaysDisplaySorted[i], i);
    }
    return result;
  }, [allDaysDisplaySorted]);

  const visibleDays = useMemo(() => new Set(itemsByDay.sortedDays), [itemsByDay.sortedDays]);

  if (currentPageDays.length === 0) {
    return null;
  }

  return (
    <div>
      <div className={styles.controls}>
        <label>
          Gap min:
          <input
            className={styles.gapMinInput}
            type="number"
            value={gapMin}
            min={0}
            step={10}
            onChange={e =>
              mutateAppState(d => {
                d.gapMin = e.target.valueAsNumber || 0;
              })
            }
          />
        </label>
        <label>
          <input
            type="checkbox"
            disabled={busy}
            checked={sortAscending}
            onChange={e => {
              mutateAppState(d => {
                d.sortAscending = e.target.checked;
              });
            }}
          />
          Sort Ascending
        </label>
      </div>
      <div id="pagination-top-scroll" />
      <Paginator
        itemsByDay={itemsByDay}
        isTopPaginator={true}
        topScrollSelector="#pagination-top-scroll"
        bottomScrollSelector="#pagination-bottom-scroll"
      />
      <div className={styles.days}>
        {gapControls(allDaysDisplaySorted, dayToIndex, visibleDays, "", currentPageDays[0], false)}
        {currentPageDays.map((day, i) => {
          const prevVisibleDay = itemsByDay.sortedDays[pageOffset + i - 1];
          const nextVisibleDay = itemsByDay.sortedDays[pageOffset + i + 1];

          return (
            <Fragment key={day}>
              <Day
                day={day}
                prevVisibleDay={prevVisibleDay}
                nextVisibleDay={nextVisibleDay}
                items={itemsByDay.items[day]}
                filtered={filtered}
                tagTotals={tagTotalsByDay ? tagTotalsByDay[day] : undefined}
                reverseSort={reverseSort}
                scrollToDay={scrollToDay}
              />
              {gapControls(
                allDaysDisplaySorted,
                dayToIndex,
                visibleDays,
                day,
                currentPageDays[i + 1],
                i + 1 === pageSize
              )}
            </Fragment>
          );
        })}
      </div>
      <Paginator
        itemsByDay={itemsByDay}
        isTopPaginator={false}
        topScrollSelector="#pagination-top-scroll"
        bottomScrollSelector="#pagination-bottom-scroll"
      />
      <div id="pagination-bottom-scroll" />
    </div>
  );
};

function showDay(day: string) {
  mutateAppState(d => {
    d.forceFullDays.push(day);
  });
}

function gapControls(
  allDaysDisplaySorted: string[],
  dayToIndex: Map<string, number>,
  visibleDays: Set<string>,
  d1: string,
  d2: string,
  mustGoToNextPage: boolean
) {
  const d1Index = dayToIndex.get(d1);
  const d2Index = dayToIndex.get(d2);

  let gapDay1 = "";
  let gapDay2 = "";
  let gapDays = 0;
  let bothButtons = false;
  let button1Icon = "↓";

  if (d1Index === undefined) {
    // top of list
    if (d2Index === undefined) {
      throw Error("BUG");
    }
    if (d2Index === 0) {
      return null;
    }
    const prevDay = allDaysDisplaySorted[d2Index - 1];
    if (!visibleDays.has(prevDay)) {
      gapDay1 = prevDay;
      button1Icon = "↑";
    }
  } else if (d2Index === undefined) {
    // bottom of list
    if (d1Index === allDaysDisplaySorted.length - 1) {
      return null;
    }
    const nextDay = allDaysDisplaySorted[d1Index + 1];
    if (!visibleDays.has(nextDay)) {
      gapDay1 = nextDay;
    }
  } else {
    // gap between items in list
    gapDays = d2Index - d1Index - 1;
    if (gapDays < 1) {
      return null;
    }
    if (gapDays > 1) {
      bothButtons = true;
    } else {
      button1Icon = "↑↓";
    }
    gapDay1 = allDaysDisplaySorted[d1Index + 1];
    gapDay2 = allDaysDisplaySorted[d2Index - 1];
  }

  if (!gapDay1) return null;

  function handleGapDay1Click() {
    showDay(gapDay1);
    if (mustGoToNextPage) {
      mutateAppState(d => {
        d.pageIndex++;
      });
      requestAnimationFrame(() => {
        document.getElementById("day-" + gapDay1)!.scrollIntoView();
      });
    }
  }

  return (
    <div className={styles.gapControls}>
      <button onClick={handleGapDay1Click}>{`${button1Icon} ${gapDay1}`}</button>
      {bothButtons && (
        <>
          {gapDays > 2 && <>…{gapDays - 2}…</>}
          <button onClick={() => showDay(gapDay2)}>↑ {gapDay2}</button>
        </>
      )}
    </div>
  );
}

let Days: React.FC<Props> = props => {
  return props.items.length ? (
    <DaysInner {...props} />
  ) : (
    <div className={styles.nothing}>(nothing found)</div>
  );
};
Days = React.memo(Days);

export default Days;
