import React from "react";
import { mutateAppState, useAppState } from "./store";
import styles from "./Paginator.module.scss";
import { TrackingItemsByDay } from "./types";

interface Props {
  itemsByDay: TrackingItemsByDay;
  isTopPaginator: boolean;
  topScrollSelector: string;
  bottomScrollSelector: string;
}

let Paginator: React.FC<Props> = ({
  isTopPaginator,
  topScrollSelector,
  bottomScrollSelector,
  itemsByDay,
}) => {
  const busy = useAppState(o => o.busy) > 0;
  const pageIndex = useAppState(o => o.pageIndex);
  const pageSize = 10;
  const maxPageIndex = Math.ceil(itemsByDay.sortedDays.length / pageSize) - 1;

  function setPageIndex(n: number) {
    mutateAppState(d => {
      d.pageIndex = n;
    });
  }

  function scrollTop() {
    if (topScrollSelector) {
      requestAnimationFrame(() => {
        document.querySelector(topScrollSelector)!.scrollIntoView();
      });
    }
  }

  function scrollBottom() {
    if (bottomScrollSelector) {
      requestAnimationFrame(() => {
        document.querySelector(bottomScrollSelector)!.scrollIntoView();
      });
    }
  }

  function handleFirstPageClick() {
    setPageIndex(0);
    if (!isTopPaginator) {
      scrollBottom();
    }
  }

  function handleFirstPageAndScrollClick() {
    setPageIndex(0);
    scrollTop();
  }

  function handlePrevPageClick() {
    setPageIndex(pageIndex - 1);
    if (!isTopPaginator) {
      scrollBottom();
    }
  }

  function handlePrevPageAndScrollClick() {
    setPageIndex(pageIndex - 1);
    scrollBottom();
  }

  function handleNextPageClick() {
    setPageIndex(pageIndex + 1);
    if (!isTopPaginator) {
      scrollBottom();
    }
  }

  function handleNextPageAndScrollClick() {
    setPageIndex(pageIndex + 1);
    scrollTop();
  }

  function handleLastPageClick() {
    setPageIndex(maxPageIndex);
    if (!isTopPaginator) {
      scrollBottom();
    }
  }

  function handleLastPageAndScrollClick() {
    setPageIndex(maxPageIndex);
    scrollBottom();
  }

  return itemsByDay.sortedDays.length === 0 ? null : (
    <div className={styles.root}>
      <div className={styles.pageNumber}>
        Page {pageIndex + 1} of {maxPageIndex + 1} ({itemsByDay.sortedDays.length} days)
      </div>
      <div className={styles.buttons}>
        {!isTopPaginator && (
          <button
            onClick={handleFirstPageAndScrollClick}
            disabled={busy || (pageIndex === 0 && isTopPaginator)}
            title="first page and scroll to top"
          >
            ↑&lt;&lt;
          </button>
        )}
        <button
          onClick={handleFirstPageClick}
          disabled={busy || pageIndex === 0}
          title="first page"
        >
          &lt;&lt;
        </button>
        {isTopPaginator && (
          <button
            onClick={handlePrevPageAndScrollClick}
            disabled={busy || pageIndex === 0}
            title="previous page and scroll to bottom"
          >
            ↓&lt;
          </button>
        )}
        <button
          onClick={handlePrevPageClick}
          disabled={busy || pageIndex === 0}
          title="previous page"
        >
          &lt;
        </button>
        <button
          onClick={handleNextPageClick}
          disabled={busy || pageIndex === maxPageIndex}
          title="next page"
        >
          &gt;
        </button>
        {!isTopPaginator && (
          <button
            onClick={handleNextPageAndScrollClick}
            disabled={busy || pageIndex === maxPageIndex}
            title="next page and scroll to top"
          >
            &gt;↑
          </button>
        )}
        <button
          onClick={handleLastPageClick}
          disabled={busy || pageIndex === maxPageIndex}
          title="last page"
        >
          &gt;&gt;
        </button>
        {isTopPaginator && (
          <button
            onClick={handleLastPageAndScrollClick}
            disabled={busy || (pageIndex === maxPageIndex && !isTopPaginator)}
            title="last page and scroll to bottom"
          >
            &gt;&gt;↓
          </button>
        )}
      </div>
    </div>
  );
};
Paginator = React.memo(Paginator);

export default Paginator;
