import React, { ChangeEvent, Fragment, useCallback, useEffect, useRef, useState } from "react";
import { LOCAL_STORAGE_TAGGERS_KEY, mutateAppState, TagShowMode, useAppState } from "./store";
import styles from "./Taggers.module.scss";
import { Tagger } from "./types";
import { errorString } from "./utils";
import { loadSettings, saveSettings } from "./sheetapi";
import produce from "immer";
import { useDebounce } from "./hooks";

const Taggers: React.FC = () => {
  const busy = useAppState(o => o.busy) > 0;
  const taggers = useAppState(o => o.taggers);
  const showTaggers = useAppState(o => o.showTaggers);
  const tagShowMode = useAppState(o => o.tagShowMode);
  const [asJson, setAsJson] = useState(false);
  const [jsonText, setJsonText] = useState<string | undefined>();
  const [jsonError, setJsonError] = useState("");
  const tagTotalsIncludeFiltered = useAppState(o => o.tagTotalsIncludeFiltered);

  const [localTaggers, setLocalTaggers] = useState(taggers);
  useEffect(() => {
    setLocalTaggers(taggers);
  }, [taggers]);

  const prevAsJson = useRef(asJson);
  useEffect(() => {
    window.localStorage[LOCAL_STORAGE_TAGGERS_KEY] = JSON.stringify(taggers);

    if (asJson && !prevAsJson.current) {
      setJsonText(JSON.stringify(taggers, null, 3));
    } else if (!asJson && prevAsJson.current) {
      setJsonText("");
      setJsonError("");
    }

    prevAsJson.current = asJson;
  }, [taggers, asJson]);

  useEffect(() => {
    if (!showTaggers) {
      setAsJson(false);
    }
  }, [showTaggers]);

  function handleAddClick() {
    mutateAppState(d => {
      d.taggers.push({
        includePattern: "",
        excludePattern: "",
        unitName: "",
      });
    });
  }

  function handleToggleClick() {
    mutateAppState(d => {
      d.showTaggers = !d.showTaggers;
    });
  }

  function jsonChanged(e: ChangeEvent<HTMLTextAreaElement>) {
    setJsonText(e.target.value);

    try {
      const newTaggers: Tagger[] = [];
      const parsed = JSON.parse(e.target.value) as Tagger[];
      for (const tagger of parsed) {
        newTaggers.push({
          includePattern: tagger.includePattern,
          excludePattern: tagger.excludePattern,
          unitName: tagger.unitName,
        });
      }
      mutateAppState(d => {
        d.taggers = newTaggers;
      });
      setJsonError("");
    } catch (e) {
      setJsonError(errorString(e));
    }
  }

  async function handleSaveClick() {
    await saveSettings({ taggers });
  }

  async function handleLoadClick() {
    const settings = await loadSettings();
    mutateAppState(d => {
      d.taggers = settings.taggers.map(o => ({
        includePattern: o.includePattern,
        excludePattern: o.excludePattern,
        unitName: o.unitName,
      }));
    });
  }

  useDebounce(
    localTaggers,
    500,
    useCallback(newTaggers => {
      mutateAppState(d => {
        d.taggers = newTaggers;
      });
    }, [])
  );

  function mutateTaggers(immerCallback: (taggers: Tagger[]) => void) {
    const updatedTaggers = produce(taggers, immerCallback);
    setLocalTaggers(updatedTaggers);
  }

  return (
    <div className={styles.root}>
      <div className={styles.buttons}>
        <label className={styles.showTags}>
          Show tags:
          <select
            value={tagShowMode}
            onChange={e =>
              mutateAppState(d => {
                d.tagShowMode = e.target.value as TagShowMode;
              })
            }
          >
            <option value="NONE">None</option>
            <option value="DAY_SUMMARIES">Day summaries</option>
            <option value="DAY_SUMMARIES_ONLY">Day summaries only</option>
            <option value="ALL">All</option>
          </select>
        </label>
        <button onClick={handleToggleClick}>Toggle Taggers Editor</button>
        {showTaggers && (
          <>
            <button disabled={busy} onClick={() => setAsJson(b => !b)}>
              as JSON
            </button>
            {!asJson && (
              <>
                <button disabled={busy} onClick={handleAddClick}>
                  Add Tagger
                </button>
                <button disabled={busy} onClick={handleSaveClick}>
                  Save
                </button>
                <button disabled={busy} onClick={handleLoadClick}>
                  Load
                </button>
              </>
            )}
          </>
        )}
      </div>
      {showTaggers &&
        (asJson ? (
          <>
            <textarea
              disabled={busy}
              className={styles.jsonEditor}
              value={jsonText}
              onChange={jsonChanged}
              spellCheck={false}
            />
            <div className={styles.jsonError}>{jsonError || <>&nbsp;</>}</div>
          </>
        ) : (
          <div className={styles.inputs}>
            {localTaggers.map((tagger, i) => (
              <Fragment key={i}>
                <input
                  disabled={busy}
                  placeholder="unit name"
                  type="text"
                  value={tagger.unitName}
                  onChange={e => {
                    mutateTaggers(d => {
                      d[i].unitName = e.target.value;
                    });
                  }}
                />
                <input
                  disabled={busy}
                  spellCheck={false}
                  className={styles.patternInput}
                  placeholder="include"
                  type="text"
                  value={tagger.includePattern}
                  onChange={e => {
                    mutateTaggers(d => {
                      d[i].includePattern = e.target.value;
                    });
                  }}
                />
                <input
                  disabled={busy}
                  spellCheck={false}
                  className={styles.patternInput}
                  placeholder="exclude"
                  type="text"
                  value={tagger.excludePattern}
                  onChange={e => {
                    mutateTaggers(d => {
                      d[i].excludePattern = e.target.value;
                    });
                  }}
                />
              </Fragment>
            ))}
          </div>
        ))}
      <div className={styles.bottomCheck}>
        <label>
          <input
            type="checkbox"
            checked={tagTotalsIncludeFiltered}
            onChange={e =>
              mutateAppState(d => {
                d.tagTotalsIncludeFiltered = e.target.checked;
              })
            }
          />
          Include filtered items in totals
        </label>
      </div>
    </div>
  );
};

export default Taggers;
