import { TrackingItem } from "./types";
import { mutateAppState } from "./store";
import { updateItemKey } from "./sheetapi";
import { formatDay } from "./utils";

const LOCAL_STORAGE_ITEMS_KEY = "notes_items";

interface localStorageItems {
  strings: Record<number, string>;
  items: number[][]; // [spreadsheetId, rowNum, date, text]
}

export function saveItemsToLocalStorage(items: TrackingItem[]) {
  console.time("save items to localStorage");

  let lastStringId = 0;
  const stringsIds = new Map<string, number>();

  const storageItems: localStorageItems = {
    strings: {},
    items: [],
  };

  const getStringId = (s: string): number => {
    let id = stringsIds.get(s);
    if (id === undefined) {
      id = ++lastStringId;

      storageItems.strings[id] = s;
      stringsIds.set(s, id);
    }
    return id;
  };

  for (const item of items) {
    storageItems.items.push([
      getStringId(item.spreadsheetId),
      item.rowNum,
      item.date.valueOf(),
      getStringId(item.text),
    ]);
  }

  window.localStorage[LOCAL_STORAGE_ITEMS_KEY] = JSON.stringify(storageItems);

  console.timeEnd("save items to localStorage");
}

export function loadItemsFromLocalStorage() {
  try {
    console.time("load items from localStorage");
    const savedItems = JSON.parse(
      window.localStorage[LOCAL_STORAGE_ITEMS_KEY] || "null"
    ) as localStorageItems | null;

    if (!savedItems) {
      return;
    }

    const getString = (id: number): string => {
      return savedItems.strings[id];
    };

    const items: TrackingItem[] = [];

    for (const savedItem of savedItems.items) {
      const [spreadsheetIdId, rowNum, dateId, textId] = savedItem;

      const date = new Date(dateId);
      const item: TrackingItem = {
        key: "",
        spreadsheetId: getString(spreadsheetIdId),
        rowNum,
        text: getString(textId),
        date,
        day: formatDay(date),
      };

      updateItemKey(item);

      items.push(item);
    }
    console.timeEnd("load items from localStorage");
    mutateAppState(d => {
      d.trackingItems = items;
    });
  } catch {}
}
