import { useSelector } from "react-redux";
import { Store, createStore } from "redux";
import produce, { isDraft } from "immer";
import { Tagger, TrackingItem } from "./types";

export const LOCAL_STORAGE_TEXT_KEY = "notes_new_item_text";
export const LOCAL_STORAGE_TAGGERS_KEY = "notes_taggers";
export const LOCAL_STORAGE_GAP_MIN_KEY = "notes_gap_min";

declare global {
  interface Window {
    __REDUX_DEVTOOLS_EXTENSION__?: () => any;
  }
}

export type TagShowMode = "NONE" | "ALL" | "DAY_SUMMARIES" | "DAY_SUMMARIES_ONLY";

export interface AppState {
  busy: number;
  errors: string[];
  filter: string;
  filterCaseSensitive: boolean;
  filterShowFullDays: boolean;
  filterUsingHighlight: boolean;
  forceFullDays: string[];
  gapMin: number;
  googleSignedIn: boolean;
  highlight: string;
  highlightCaseSensitive: boolean;
  newItemText: string;
  pageIndex: number;
  referenceDate: Date;
  referenceDateNow: boolean;
  selectedRange: Date[] | undefined;
  showTaggers: boolean;
  showTimeline: boolean;
  sortAscending: boolean;
  taggers: Tagger[];
  tagShowMode: TagShowMode;
  tagTotalsIncludeFiltered: boolean;
  trackingItems: TrackingItem[];
}

interface storeAction {
  type: "IMMER";
  callback: (draft: AppState) => AppState;
}

export type storeType = Store<AppState, storeAction>;

export const reduxStore: storeType = createStore(
  (state: AppState | undefined, action: storeAction): AppState => {
    if (!state) {
      state = {
        busy: 0,
        errors: [],
        filter: "",
        filterCaseSensitive: false,
        filterShowFullDays: false,
        filterUsingHighlight: true,
        forceFullDays: [],
        gapMin: loadGapMin(),
        googleSignedIn: false,
        highlight: "",
        highlightCaseSensitive: false,
        newItemText: localStorage[LOCAL_STORAGE_TEXT_KEY] || "",
        pageIndex: 0,
        referenceDate: new Date(),
        referenceDateNow: true,
        selectedRange: undefined,
        showTaggers: false,
        showTimeline: false,
        sortAscending: false,
        taggers: loadTaggers(),
        tagShowMode: "DAY_SUMMARIES",
        tagTotalsIncludeFiltered: true,
        trackingItems: [],
      };
    }

    if (action.type === "IMMER") {
      state = produce(state, action.callback);
    }

    return state;
  },
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

// call this function to mutate the global Redux store instance
export function mutateAppState(cb: (a: AppState) => void) {
  reduxStore.dispatch({
    type: "IMMER",
    callback: (o: AppState) => produce(o, cb),
  });
}

// React custom hook:
// const appState = useAppState();
export function useAppState<T = AppState>(filter?: (appState: AppState) => T) {
  if (!filter) {
    filter = o => o as any;
  }
  return useSelector(filter) as T;
}

// eslint-disable-next-line @typescript-eslint/no-unused-vars
function ensureImmerDraft(o: any) {
  if (!isDraft(o)) throw Error("argument must be an immer draft");
}

function loadTaggers(): Tagger[] {
  try {
    console.time("loadTaggers");
    const obj = JSON.parse(localStorage[LOCAL_STORAGE_TAGGERS_KEY]) as Tagger[];
    return obj.map(o => ({
      excludePattern: o.excludePattern,
      includePattern: o.includePattern,
      unitName: o.unitName,
    }));
  } catch (e) {
    return [];
  } finally {
    console.timeEnd("loadTaggers");
  }
}

function loadGapMin() {
  try {
    const num = parseInt(localStorage[LOCAL_STORAGE_GAP_MIN_KEY], 10);
    if (num >= 0) return num;
  } catch (e) {}
  return 0;
}
