import { mutateAppState } from "./store";
import { Subject } from "rxjs";

const resetSubject = new Subject<void>();
export const resetObservable = resetSubject.asObservable();

export function resetUI() {
  resetSubject.next();

  mutateAppState(d => {
    d.filter = "";
    d.filterCaseSensitive = false;
    d.filterShowFullDays = false;
    d.filterUsingHighlight = true;
    d.forceFullDays = [];
    d.highlight = "";
    d.highlightCaseSensitive = false;
    d.newItemText = "";
    d.pageIndex = 0;
    d.selectedRange = undefined;
    d.showTaggers = false;
    d.showTimeline = false;
    d.sortAscending = false;
    d.tagShowMode = "DAY_SUMMARIES";
    d.tagTotalsIncludeFiltered = true;
  });

  document.getElementById("scroll-root")!.scrollTo(0, 0);
}
