import React, { useLayoutEffect, useMemo } from "react";
import * as sheetapi from "./sheetapi";
import styles from "./Sheet.module.scss";
import Days from "./Days";
import Filter from "./Filter";
import Timeline from "./Timeline";
import {
  createGetItemsByDay,
  getFilteredItems,
  getFilterFromHighlight,
  getItemsInRange,
  getTaggedItems,
  getTagTotalsByDay,
} from "./memos";
import RefTime from "./RefTime";
import NewItem from "./NewItem";
import { mutateAppState, useAppState } from "./store";
import GlobalShortcuts from "./GlobalShortcuts";
import Highlight from "./Highlight";
import Taggers from "./Taggers";
import { resetUI } from "./reset";
import { differenceInCalendarDays } from "date-fns";
import { TagTotalsByDay } from "./types";

const Sheet: React.FC = () => {
  const trackingItems = useAppState(o => o.trackingItems);
  const taggers = useAppState(o => o.taggers);
  const selectedRange = useAppState(o => o.selectedRange);
  const busy = useAppState(o => o.busy) > 0;
  const filter = useAppState(o => o.filter);
  const filterCaseSensitive = useAppState(o => o.filterCaseSensitive);
  const filterShowFullDays = useAppState(o => o.filterShowFullDays);
  const filterUsingHighlight = useAppState(o => o.filterUsingHighlight);
  const forceFullDays = useAppState(o => o.forceFullDays);
  const highlight = useAppState(o => o.highlight);
  const highlightCaseSensitive = useAppState(o => o.highlightCaseSensitive);
  const showTimeline = useAppState(o => o.showTimeline);
  const googleSignedIn = useAppState(o => o.googleSignedIn);
  const tagTotalsIncludeFiltered = useAppState(o => o.tagTotalsIncludeFiltered);
  const sortAscending = useAppState(o => o.sortAscending);
  const showTagTotals = useAppState(o => o.tagShowMode) !== "NONE";

  const effectiveFilter = filterUsingHighlight ? getFilterFromHighlight(highlight) : filter;
  const effectiveCaseSensitive = filterUsingHighlight
    ? highlightCaseSensitive
    : filterCaseSensitive;

  // useLayoutEffect instead of useEffect to eliminate flicker where the user can see it switch back to page 1
  useLayoutEffect(() => {
    mutateAppState(d => {
      d.pageIndex = 0;
    });
  }, [selectedRange, effectiveFilter]);

  useLayoutEffect(() => {
    mutateAppState(d => {
      if (d.forceFullDays.length) {
        d.forceFullDays = [];
      }
    });
  }, [effectiveFilter, effectiveCaseSensitive, filterShowFullDays, selectedRange]);

  function handleAuthClick() {
    gapi.auth2.getAuthInstance().signIn();
  }

  function handleReload() {
    sheetapi.loadData();
  }

  function handleTimelineToggleClick() {
    mutateAppState(d => {
      d.showTimeline = !d.showTimeline;
      if (!d.showTimeline) {
        d.selectedRange = undefined;
      }
    });
  }

  const taggedItems = getTaggedItems(trackingItems, taggers);

  const itemsToShow = getFilteredItems(
    getItemsInRange(taggedItems, selectedRange, forceFullDays),
    effectiveFilter,
    effectiveCaseSensitive,
    filterShowFullDays,
    forceFullDays
  );

  let tagsTotalsByDay: TagTotalsByDay | undefined;
  if (showTagTotals) {
    tagsTotalsByDay = getTagTotalsByDay(
      tagTotalsIncludeFiltered ? taggedItems : itemsToShow,
      taggers
    );
  }

  const getItemsByDay = useMemo(() => createGetItemsByDay("Sheet"), []);
  const allItemsByDay = getItemsByDay(trackingItems, false);

  const selectedRangeText = useMemo(() => {
    if (selectedRange) {
      const diff = differenceInCalendarDays(selectedRange[1], selectedRange[0]);
      return `${diff} day${diff > 1 ? "s" : ""} selected`;
    }
  }, [selectedRange]);

  const allDaysDisplaySorted = useMemo(() => {
    if (sortAscending) {
      return allItemsByDay.sortedDays.slice().sort();
    } else {
      return allItemsByDay.sortedDays;
    }
  }, [allItemsByDay, sortAscending]);

  return (
    <div className={styles.root}>
      <GlobalShortcuts />
      <div className={styles.version}>version {process.env.REACT_APP_BUILD_ID}</div>
      <div className={styles.topBar}>
        <button disabled={busy} onClick={handleAuthClick}>
          Auth
        </button>
        <button disabled={busy} onClick={handleReload}>
          Load
        </button>
        <span>{googleSignedIn ? "Signed in" : "Not signed in"}</span>
        <button onClick={resetUI} title="reset UI to startup state | key: Esc Esc">
          Reset
        </button>
      </div>
      <div className={styles.controlBar}>
        <NewItem />
      </div>
      <div className={styles.controlBar}>
        <Highlight />
      </div>
      <div className={styles.controlBar}>
        <button onClick={handleTimelineToggleClick}>Toggle Timeline</button>
        {selectedRangeText && <span className={styles.selectedRangeText}>{selectedRangeText}</span>}
      </div>
      <div className={styles.timeline}>
        {showTimeline && <Timeline itemsByDay={allItemsByDay} />}
      </div>
      <Filter />
      <Taggers />
      <div className={styles.controlBar}>
        <RefTime />
      </div>
      <Days
        allDaysDisplaySorted={allDaysDisplaySorted}
        tagTotalsByDay={tagsTotalsByDay}
        items={itemsToShow}
        filtered={!filterShowFullDays && effectiveFilter.length > 0}
      />
    </div>
  );
};

export default Sheet;
