import React, { useCallback, useEffect, useState } from "react";
import { mutateAppState, useAppState } from "./store";
import { getFilterFromHighlight } from "./memos";
import { useDebounce } from "./hooks";

const Filter: React.FC = () => {
  const filter = useAppState(o => o.filter);
  const highlight = useAppState(o => o.highlight);
  const showFullDays = useAppState(o => o.filterShowFullDays);
  const filterCaseSensitive = useAppState(o => o.filterCaseSensitive);
  const filterUsingHighlight = useAppState(o => o.filterUsingHighlight);
  const highlightCaseSensitive = useAppState(o => o.highlightCaseSensitive);
  const [pendingFilter, setPendingFilter] = useState(filter);

  useEffect(() => {
    setPendingFilter(filter);
  }, [filter]);

  useDebounce(
    pendingFilter,
    200,
    useCallback(s => {
      mutateAppState(d => {
        d.filter = s;
      });
    }, [])
  );

  function handleFromHighlightClick() {
    mutateAppState(d => {
      d.filter = getFilterFromHighlight(d.highlight);
      d.filterCaseSensitive = d.highlightCaseSensitive;
    });
  }

  function handleKeyDown(e: React.KeyboardEvent<HTMLInputElement>) {
    if (e.key === "Escape") {
      mutateAppState(d => {
        d.filter = "";
      });
    }
  }

  function handleClearClick() {
    mutateAppState(d => {
      d.filter = "";
      d.filterShowFullDays = false;
    });
  }

  return (
    <div>
      <button disabled={filterUsingHighlight || filter.length === 0} onClick={handleClearClick}>
        Clear
      </button>
      <button
        disabled={filterUsingHighlight || highlight.length === 0}
        onClick={handleFromHighlightClick}
      >
        From Highlight
      </button>
      <input
        disabled={filterUsingHighlight}
        autoCapitalize="off"
        spellCheck={false}
        placeholder={filterUsingHighlight ? "(using highlight)" : "filter regexp"}
        type="text"
        value={filterUsingHighlight ? "" : pendingFilter}
        onChange={e => setPendingFilter(e.target.value)}
        onKeyDown={handleKeyDown}
      />
      <label style={{ display: "inline-block", marginTop: "0.5em" }}>
        <input
          type="checkbox"
          checked={showFullDays}
          onChange={e =>
            mutateAppState(d => {
              d.filterShowFullDays = e.target.checked;
            })
          }
        />
        Show full days
      </label>
      <label style={{ display: "inline-block", marginTop: "0.5em" }}>
        <input
          type="checkbox"
          disabled={filterUsingHighlight}
          checked={filterUsingHighlight ? highlightCaseSensitive : filterCaseSensitive}
          onChange={e =>
            mutateAppState(d => {
              d.filterCaseSensitive = !d.filterCaseSensitive;
            })
          }
        />
        Case sensitive
      </label>
      <label style={{ display: "inline-block", marginTop: "0.5em" }}>
        <input
          type="checkbox"
          checked={filterUsingHighlight}
          onChange={e =>
            mutateAppState(d => {
              d.filterUsingHighlight = e.target.checked;
            })
          }
        />
        Filter using highlight
      </label>
    </div>
  );
};

export default Filter;
