import { useEffect, useRef } from "react";
import { Subject } from "rxjs";
import { debounceTime } from "rxjs/operators";

export function useDebounce<T>(stateValue: T, delayMS: number, callback: (debounced: T) => void) {
  interface Pending {
    subject: Subject<T>;
    prev: T;
  }

  const pending = useRef<Pending>();
  if (!pending.current) {
    pending.current = {
      subject: new Subject<T>(),
      prev: stateValue,
    };
  }

  if (pending.current!.prev !== stateValue) {
    pending.current!.subject.next(stateValue);
    pending.current!.prev = stateValue;
  }

  useEffect(() => {
    const sub = pending.current!.subject.pipe(debounceTime(delayMS)).subscribe(callback);
    return () => {
      sub.unsubscribe();
    };
  }, [delayMS, callback]);
}
