import React, { Fragment, useMemo } from "react";
import { differenceInCalendarDays, differenceInMinutes } from "date-fns";
import styles from "./Day.module.scss";
import Item from "./Item";
import { Tag, TrackingItem } from "./types";
import { mutateAppState, useAppState } from "./store";
import { formatDayForHuman } from "./utils";

interface Props {
  day: string;
  prevVisibleDay: string;
  nextVisibleDay: string;
  items: TrackingItem[];
  filtered: boolean;
  tagTotals: Tag[] | undefined;
  reverseSort: (day: string) => void;
  scrollToDay: (day: string) => void;
}

let Day: React.FC<Props> = ({
  day,
  prevVisibleDay,
  nextVisibleDay,
  items,
  filtered,
  tagTotals,
  reverseSort,
  scrollToDay,
}) => {
  const formattedDate = formatDayForHuman(items[0].date);
  const daysAgo = differenceInCalendarDays(new Date(), items[0].date);
  const tagShowMode = useAppState(o => o.tagShowMode);
  const gapMin = useAppState(o => o.gapMin);
  const filterShowFullDays = useAppState(o => o.filterShowFullDays);
  const forceFullDays = useAppState(o => o.forceFullDays);
  const sortAscending = useAppState(o => o.sortAscending);

  function handleHeaderClick(e: React.MouseEvent<HTMLDivElement>) {
    const scrollRoot = document.getElementById("scroll-root")!;
    scrollRoot.scrollTo(0, 0);
    e.preventDefault();
    e.stopPropagation();
  }

  function handleScrollButtonClick(e: React.MouseEvent<HTMLButtonElement>, dayToScroll: string) {
    e.stopPropagation();
    scrollToDay(dayToScroll);
  }

  function handleReverseClick(e: React.MouseEvent<HTMLButtonElement>) {
    e.stopPropagation();
    reverseSort(day);
  }

  function handleToggleFullDayClick(e: React.MouseEvent<HTMLButtonElement>) {
    e.stopPropagation();
    const dayElem = document.getElementById("day-" + day)!;
    const dayTop = dayElem.getBoundingClientRect().top;
    mutateAppState(d => {
      if (d.forceFullDays.includes(day)) {
        d.forceFullDays = d.forceFullDays.filter(o => o !== day);
      } else {
        d.forceFullDays.push(day);
      }
    });

    if (dayTop < 0) {
      dayElem.scrollIntoView();
    }
  }

  const dayIsForceShown = useMemo(() => forceFullDays.includes(day), [forceFullDays, day]);

  return (
    <div id={"day-" + day} className={styles.root + " day"}>
      {/* We need this border here ↓↓↓ otherwise the position when sticky vs. the position when scrolled to is off by 1px */}
      <div className={styles.topBorder} />
      <div className={styles.dayHeader}>
        <span onClick={handleHeaderClick} className={styles.displayDate}>
          {formattedDate},{" "}
          {daysAgo === 0 ? (
            "today"
          ) : daysAgo === 1 ? (
            "yesterday"
          ) : (
            <span>{daysAgo}&nbsp;days&nbsp;ago</span>
          )}
        </span>
        {dayIsForceShown || (filtered && !filterShowFullDays) ? (
          <button
            onClick={handleToggleFullDayClick}
            title="toggle full day"
            className={styles.toggleFullDayButton}
          >
            <svg
              height="1.2em"
              width="1.2em"
              viewBox="0 0 100 100"
              transform="translate(-0.8,0.75) scale(1.05)"
            >
              <path
                transform="translate(-0.18407236,-15.880162)"
                d="m 47,31.1 v 23.903057 h 6 V 31.1 l 16.423971,15.994168 4.2,-4.3 L 50,19.6 27.04842,42.572481 l 4.2,4.3 z"
              />
              <rect id="rect934" width="67.770233" height="4.7536263" x="15.795475" y="44.614605" />
              <path
                id="path936"
                d="M 53.488319,79.672898 V 55.769841 h -6 V 79.672898 L 31.064348,63.67873 l -4.2,4.3 23.623971,23.194168 22.95158,-22.972481 -4.2,-4.3 z"
              />
            </svg>
          </button>
        ) : (
          <span />
        )}
        <div className={styles.pageButtons}>
          <button onClick={handleReverseClick} title="reverse sort">
            <svg
              height="1.2em"
              width="1.2em"
              viewBox="0 0 24 24"
              focusable={false}
              pointerEvents="none"
            >
              <path
                d="M0 4.41422L4.41418 0L5.82837 1.41422L3.82837 3.41422H11.4142C15.2802 3.41422 18.4142 6.54822 18.4142 10.4142C18.4142 14.2802 15.2802 17.4142 11.4142 17.4142H2.41418V15.4142H11.4142C14.1757 15.4142 16.4142 13.1756 16.4142 10.4142C16.4142 7.65279 14.1757 5.41422 11.4142 5.41422H3.82837L5.82837 7.41422L4.41418 8.82843L0 4.41422Z"
                transform={`translate(2.58582 22.58578) rotate(${
                  sortAscending ? "90,19,0" : "-90"
                })`}
                fill="black"
              />
            </svg>
          </button>
          <button
            disabled={prevVisibleDay === undefined}
            onClick={e => handleScrollButtonClick(e, prevVisibleDay)}
            title={"scroll to prev day " + prevVisibleDay}
          >
            <svg
              height="1.2em"
              width="1.2em"
              viewBox="0 0 100 100"
              transform="scale(1.25)"
              focusable={false}
              pointerEvents="none"
            >
              <polygon points="27.9,50.2 47,31.1 47,80.4 53,80.4 53,31.1 72.1,50.2 76.3,45.9 50,19.6 23.7,45.9" />
            </svg>
          </button>
          <button
            disabled={nextVisibleDay === undefined}
            onClick={e => handleScrollButtonClick(e, nextVisibleDay)}
            title={"scroll to next day " + nextVisibleDay}
          >
            <svg
              height="1.2em"
              width="1.2em"
              viewBox="0 0 100 100"
              transform="rotate(180) scale(1.25)"
              focusable={false}
              pointerEvents="none"
            >
              <polygon points="27.9,50.2 47,31.1 47,80.4 53,80.4 53,31.1 72.1,50.2 76.3,45.9 50,19.6 23.7,45.9" />
            </svg>
          </button>
        </div>
      </div>
      {tagTotals && tagTotals.length > 0 && (
        <div className={styles.tagTotals}>
          {tagTotals.map(tag => (
            <div key={tag.unitName} className={styles.tag}>
              {tag.quantity} {tag.unitName}
            </div>
          ))}
        </div>
      )}
      {tagShowMode !== "DAY_SUMMARIES_ONLY" &&
        items.map((item, i) => (
          <Fragment key={item.key}>
            <Item item={item} tagShowMode={tagShowMode} />
            {!filtered && gapMin > 0 && i + 1 < items.length && gap(gapMin, item, items[i + 1])}
          </Fragment>
        ))}
    </div>
  );
};
Day = React.memo(Day);

export default Day;

function gap(gapMin: number, a: TrackingItem, b: TrackingItem) {
  const diff = Math.abs(differenceInMinutes(a.date, b.date));
  if (diff < gapMin) return null;
  const height = Math.sqrt(diff * 10) + 5;

  return (
    <>
      <div className={styles.gap1} style={{ height }}>
        &nbsp;
      </div>
      <div className={styles.gap2} style={{ height }}>
        &nbsp;
      </div>
    </>
  );
}
