import { Tag, Tagger, TagTotalsByDay, TrackingItem, TrackingItemsByDay } from "./types";
import memoizeOne from "memoize-one";
import produce from "immer";

function memo<T extends (...args: any[]) => any>(name: string, fn: T): T {
  return memoizeOne(function (...args: any[]) {
    try {
      console.time(name);
      return fn(...args);
    } finally {
      console.timeEnd(name);
    }
  }) as unknown as T;
}

export const makeGetHighlights = memo(
  "makeGetHighlights",
  (patternSpec: string, caseSensitive: boolean) => {
    let patterns: string[] = [];
    let regexps: RegExp[] = [];
    if (patternSpec) {
      try {
        for (const pattern of patternSpec.split("\n")) {
          if (pattern) {
            const re = new RegExp(pattern, caseSensitive ? "" : "i");
            patterns.push(pattern);
            regexps.push(re);
          }
        }
      } catch (e) {}
    }
    return {
      highlightPatterns: patterns,
      getHighlights: (s: string) => {
        const result: number[] = [];
        for (let i = 0; i < regexps.length; i++) {
          if (regexps[i].test(s)) {
            result.push(i);
          }
        }
        return result;
      },
    };
  }
);

export type GetItemsByDayFunc = (
  items: TrackingItem[],
  sortAscending: boolean
) => TrackingItemsByDay;

export const createGetItemsByDay = (usageName: string): GetItemsByDayFunc => {
  return memo("getItemsByDay: " + usageName, (items: TrackingItem[], sortAscending: boolean) => {
    const itemsByDay: {
      [day: string]: TrackingItem[];
    } = {};
    for (const item of items) {
      let forDay = itemsByDay[item.day];
      if (!forDay) {
        forDay = [];
        itemsByDay[item.day] = forDay;
      }
      forDay.push(item);
    }

    const sortedDays: string[] = [];
    for (const day in itemsByDay) {
      sortedDays.push(day);

      itemsByDay[day].sort((a, b) => {
        if (sortAscending) {
          return a.date.valueOf() - b.date.valueOf();
        } else {
          return b.date.valueOf() - a.date.valueOf();
        }
      });
    }

    sortedDays.sort((a, b) => {
      if (sortAscending) {
        return a.localeCompare(b);
      } else {
        return b.localeCompare(a);
      }
    });

    return {
      sortedDays,
      items: itemsByDay,
    };
  });
};

export const getItemsInRange = memo(
  "getItemsInRange",
  (items: TrackingItem[], selectedRange: Date[] | undefined, forceShowDays: string[]) => {
    if (!selectedRange) {
      return items;
    }

    return items.filter(
      item =>
        forceShowDays.includes(item.day) ||
        (item.date >= selectedRange[0] && item.date < selectedRange[1])
    );
  }
);

export const getFilteredItems = memo(
  "getFilteredItems",
  (
    items: TrackingItem[],
    regex: string,
    caseSensitive,
    showFullDays: boolean,
    forceFullDays: string[]
  ) => {
    if (!regex) {
      return items;
    }
    try {
      const re = new RegExp(regex, caseSensitive ? "" : "i");

      let filtered = items.filter(item => {
        if (forceFullDays.length > 0) {
          return forceFullDays.includes(item.day) || re.test(item.text);
        } else {
          return re.test(item.text);
        }
      });
      if (showFullDays) {
        const days = new Set<string>();
        for (const item of filtered) {
          days.add(item.day);
        }

        filtered = items.filter(item => {
          return days.has(item.day) || forceFullDays.includes(item.day);
        });
      }

      return filtered;
    } catch (e) {
      return [];
    }
  }
);

export const getFilterFromHighlight = memo("getFilterFromHighlight", (regex: string) => {
  return regex
    .split(/\n+/)
    .filter(s => s !== "")
    .join("|");
});

export const getTaggedItems = memo("getTaggedItems", (items: TrackingItem[], taggers: Tagger[]) => {
  return produce(items, (itemsDraft: TrackingItem[]) => {
    for (const tagger of taggers) {
      if (!tagger.includePattern) continue;

      try {
        const include = new RegExp(tagger.includePattern, "i");
        let exclude: RegExp | undefined;
        if (tagger.excludePattern) {
          exclude = new RegExp(tagger.excludePattern, "i");
        }

        for (const item of itemsDraft) {
          if (exclude && exclude.test(item.text)) continue;

          const m = include.exec(item.text);
          if (!m) continue;

          let quantity = 1;
          for (let i = 1; i < m.length; i++) {
            if (m[i]) {
              quantity = Number.parseFloat(m[i]);
              break;
            }
          }

          if (!item.tags) {
            item.tags = [];
          }
          item.tags.push({ quantity, unitName: tagger.unitName });
        }
      } catch (e) {}
    }
  });
});

export const getTagTotalsByDay = memo(
  "getTagTotalsByDay",
  (items: TrackingItem[], taggers: Tagger[]) => {
    const totalsByDayByUnit = new Map<string, Map<string, number>>();
    for (const item of items) {
      if (!item.tags) continue;

      let totalsByUnit = totalsByDayByUnit.get(item.day);
      if (!totalsByUnit) {
        totalsByUnit = new Map<string, number>();
        totalsByDayByUnit.set(item.day, totalsByUnit);
      }
      for (const tag of item.tags) {
        const current = totalsByUnit.get(tag.unitName) || 0;
        totalsByUnit.set(tag.unitName, current + tag.quantity);
      }
    }

    const results: TagTotalsByDay = {};
    for (const [day, totalsByUnit] of totalsByDayByUnit) {
      const resultsForDay: Tag[] = [];
      for (const tagger of taggers) {
        const quantity = totalsByUnit.get(tagger.unitName);
        if (typeof quantity === "number") {
          resultsForDay.push({
            unitName: tagger.unitName,
            quantity,
          });
        }
      }
      results[day] = resultsForDay;
    }

    return results;
  }
);
