import React, { memo, useState } from "react";
import styles from "./Timeline.module.scss";
import { format, addDays, differenceInCalendarDays, startOfDay } from "date-fns";
import { makeGetHighlights } from "./memos";
import { getColor } from "./colors";
import { arc } from "d3-shape";
import { TrackingItemsByDay } from "./types";
import { mutateAppState, useAppState } from "./store";

interface Props {
  itemsByDay: TrackingItemsByDay;
}

interface ColoredDay {
  date: Date;
  dayNum: number;
  x: number;
  y: number;
  colors: string[];
}

const TimelineInner: React.FC<Props> = ({ itemsByDay }) => {
  const highlight = useAppState(o => o.highlight);
  const highlightCaseSensitive = useAppState(o => o.highlightCaseSensitive);

  const { getHighlights, highlightPatterns } = makeGetHighlights(highlight, highlightCaseSensitive);

  const firstDay = itemsByDay.items[itemsByDay.sortedDays[0]][0].date;
  const firstDayNum = dayNum(firstDay);

  function dayNum(d: Date) {
    return (d.getDay() + 6) % 7;
  }

  function dayOffset(d: Date): number {
    return differenceInCalendarDays(firstDay, d);
  }

  const circleSize = 40;

  const coloredDays: ColoredDay[] = itemsByDay.sortedDays.map(day => {
    const date = itemsByDay.items[day][0].date;
    const offset = dayOffset(date);

    const colors: string[] = [];
    const seenMatchIndexes = new Set<number>();
    for (const item of itemsByDay.items[day]) {
      const matches = getHighlights(item.text);
      for (const matchIndex of matches) {
        if (matchIndex !== null && !seenMatchIndexes.has(matchIndex)) {
          colors.push(getColor(matchIndex));
          seenMatchIndexes.add(matchIndex);
        }
      }
    }

    if (colors.length === 0) {
      colors.push("white");
    }

    colors.sort();

    const thisDayNum = dayNum(date);

    const x = Math.floor((offset + (6 - firstDayNum)) / 7) * circleSize + 2;
    const y = circleSize * (6 - thisDayNum) + 2 + (thisDayNum < 5 ? 8 : 0);

    return {
      date,
      dayNum: thisDayNum,
      x,
      y,
      colors,
    };
  });

  const maxX = coloredDays.map(d => d.x).reduce((a, b) => Math.max(a, b));

  const yearLabels: Array<{ text: string; x: number }> = [];
  const weekLabels: Array<{ text: string; x: number }> = [];
  let monday = addDays(firstDay, -firstDayNum);
  const pastEnd = addDays(coloredDays[coloredDays.length - 1].date, -7);
  let x = 0;
  let prevYear = firstDay.getFullYear();
  while (monday > pastEnd) {
    const year = monday.getFullYear();
    if (year !== prevYear) {
      yearLabels.push({ text: `${prevYear} | ${year}`, x: x - circleSize });
      prevYear = year;
    }
    weekLabels.push({ text: format(monday, "LLL d"), x });
    x += circleSize;
    monday = addDays(monday, -7);
  }

  const [sel1, setSel1] = useState<ColoredDay | undefined>();
  const selectedDays = useAppState(d => d.selectedRange);

  function handleDayClick(e: React.MouseEvent<SVGGElement, MouseEvent>, day: ColoredDay) {
    e.stopPropagation();

    if (!sel1) {
      setSel1(day);
    } else {
      setSel1(undefined);
      const selectedRange = [sel1, day]
        .map(o => startOfDay(o.date))
        .sort((a, b) => a.valueOf() - b.valueOf());
      selectedRange[1] = addDays(selectedRange[1], 1);
      mutateAppState(d => {
        d.selectedRange = selectedRange;
      });
    }
  }

  function handleStrayClick() {
    if (!sel1) {
      mutateAppState(d => {
        d.selectedRange = undefined;
      });
    }
    setSel1(undefined);
  }

  return (
    <>
      <div className={styles.swatches}>
        {highlightPatterns.map((pattern, i) => (
          <span key={i} style={{ backgroundColor: getColor(i) }}>
            {pattern}
          </span>
        ))}
        &nbsp;
      </div>
      <div className={styles.root} onClick={handleStrayClick}>
        <svg style={{ height: 7 * circleSize + 100, width: maxX + 100 }}>
          {weekLabels.map(o => (
            <g
              key={o.text}
              transform={`translate(${o.x + circleSize / 2 + 33},${
                circleSize * 7 + 26
              }) rotate(-45)`}
            >
              <text textAnchor="end">{o.text}</text>
            </g>
          ))}
          {yearLabels.map(o => (
            <g key={o.text} transform={`translate(${o.x + circleSize / 2},${circleSize * 7 + 90})`}>
              <text>{o.text}</text>
            </g>
          ))}
          {coloredDays.map((day, i) => (
            <g
              key={i}
              transform={`translate(${day.x + circleSize / 2 + 20},${day.y + circleSize / 2})`}
              onClick={e => handleDayClick(e, day)}
            >
              <title>{day.date.toString()}</title>
              {sel1?.date === day.date && (
                <rect
                  className={styles.sel1}
                  x={-circleSize / 2}
                  y={-circleSize / 2}
                  width={circleSize}
                  height={circleSize}
                />
              )}
              {selectedDays && day.date >= selectedDays[0] && day.date <= selectedDays[1] && (
                <rect
                  className={styles.selRange}
                  x={-circleSize / 2}
                  y={-circleSize / 2}
                  width={circleSize}
                  height={circleSize}
                />
              )}
              {day.colors.map((c, i) => (
                <path
                  d={
                    arc()({
                      innerRadius: 0,
                      outerRadius: circleSize / 2 - 1,
                      startAngle: ((Math.PI * 2) / day.colors.length) * i,
                      endAngle: ((Math.PI * 2) / day.colors.length) * (i + 1),
                    })!
                  }
                  key={i}
                  fill={c}
                  stroke="black"
                  strokeOpacity="0.2"
                />
              ))}
              {day.date.getDate() === 1 && (
                <circle r={circleSize / 2 - 2} className={styles.firstOfMonth} />
              )}
            </g>
          ))}
        </svg>
      </div>
    </>
  );
};

const Timeline: React.FC<Props> = memo(props => {
  if (props.itemsByDay.sortedDays.length === 0) {
    return null;
  }

  return <TimelineInner {...props} />;
});

export default Timeline;
