import React, { useEffect } from "react";
import { mutateAppState, useAppState } from "./store";
import styles from "./Errors.module.scss";
import { errorString } from "./utils";

let Errors: React.FC = () => {
  const errors = useAppState(o => o.errors);

  useEffect(() => {
    const handler = (e: ErrorEvent) => {
      mutateAppState(d => {
        d.errors.push(errorString(e.error));
      });
    };

    const promiseHandler = (e: PromiseRejectionEvent) => {
      e.promise.catch(e => {
        mutateAppState(d => {
          d.errors.push(errorString(e));
        });
      });
    };

    window.addEventListener("error", handler, true);
    window.addEventListener("unhandledrejection", promiseHandler, true);

    return () => {
      window.removeEventListener("error", handler, true);
      window.removeEventListener("unhandledrejection", promiseHandler, true);
    };
  }, []);

  function clearError(index: number) {
    mutateAppState(d => {
      d.errors.splice(index, 1);
    });
  }

  return (
    <div className={styles.root}>
      {errors.map((err, i) => (
        <div className={styles.error} key={i}>
          <button onClick={() => clearError(i)}>Clear</button>
          {err}
        </div>
      ))}
    </div>
  );
};
Errors = React.memo(Errors);

export default Errors;
